		font-size: 24upx;
		vertical-align: middle;
		position: relative;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		box-sizing: border-box;
		padding: 0upx 16upx;
		height: 48upx;
		font-family: Helvetica Neue, Helvetica, sans-serif;
		white-space: nowrap;
	}
	
	.cu-tag:not([class*="bg"]):not([class*="line"]) {
		background-color: #f1f1f1;
	}
	
	.cu-tag[class*="line-"]::after {
		content: " ";
		width: 200%;
		height: 200%;
		position: absolute;
		top: 0;
		left: 0;
		border: 1upx solid currentColor;
		transform: scale(0.5);
		transform-origin: 0 0;
		box-sizing: border-box;
		border-radius: inherit;
		z-index: 1;
		pointer-events: none;
	}
	
	.cu-tag.radius[class*="line"]::after {
		border-radius: 12upx;
	}
	
	.cu-tag.round[class*="line"]::after {
		border-radius: 1000upx;
	}
	
	.cu-tag[class*="line-"]::after {
		border-radius: 0;
	}
	
	.cu-tag+.cu-tag {
		margin-left: 10upx;
	}
	
	.cu-tag.sm {
		font-size: 20upx;
		padding: 0upx 12upx;
		height: 32upx;
	}
	
	.cu-capsule {
		display: inline-flex;
		vertical-align: middle;
	}
	
	.cu-capsule+.cu-capsule {
		margin-left: 10upx;
	}
	
	.cu-capsule .cu-tag {
		margin: 0;
	}
	
	.cu-capsule .cu-tag[class*="line-"]:last-child::after {
		border-left: 0upx solid transparent;
	}
	
	.cu-capsule .cu-tag[class*="line-"]:first-child::after {
		border-right: 0upx solid transparent;
	}
	
	.cu-capsule.radius .cu-tag:first-child {
		border-top-left-radius: 6upx;
		border-bottom-left-radius: 6upx;
	}
	
	.cu-capsule.radius .cu-tag:last-child::after,
	.cu-capsule.radius .cu-tag[class*="line-"] {
		border-top-right-radius: 12upx;
		border-bottom-right-radius: 12upx;
	}
	
	.cu-capsule.round .cu-tag:first-child {
		border-top-left-radius: 200upx;
		border-bottom-left-radius: 200upx;
		text-indent: 4upx;
	}
	
	.cu-capsule.round .cu-tag:last-child::after,
	.cu-capsule.round .cu-tag:last-child {
		border-top-right-radius: 200upx;
		border-bottom-right-radius: 200upx;
		text-indent: -4upx;
	}
	
	.cu-tag.badge {
		border-radius: 200upx;
		position: absolute;
		top: -10upx;
		right: -10upx;
		font-size: 20upx;
		padding: 0upx 10upx;
		height: 28upx;
		color: #ffffff;
	}
	
	.cu-tag.badge:not([class*="bg-"]) {
		background-color: #dd514c;
	}
	
	.cu-tag:empty:not([class*="cuIcon-"]) {
		padding: 0upx;
		width: 16upx;
		height: 16upx;
		top: -4upx;
		right: -4upx;
	}
	
	.cu-tag[class*="cuIcon-"] {
		width: 32upx;
		height: 32upx;
		top: -4upx;
		right: -4upx;
	}
	
	/* ==================
	          头像
	 ==================== */
	
	.cu-avatar {
		font-variant: small-caps;
		margin: 0;
		padding: 0;
		display: inline-flex;
		text-align: center;
		justify-content: center;
		align-items: center;
		background-color: #ccc;
		color: #ffffff;
		white-space: nowrap;
		position: relative;
		width: 64upx;
		height: 64upx;
		background-size: cover;
		background-position: center;
		vertical-align: middle;
		font-size: 1.5em;
	}
	
	.cu-avatar.sm {
		width: 48upx;
		height: 48upx;
		font-size: 1em;
	}
	
	.cu-avatar.lg {
		width: 96upx;
		height: 96upx;
		font-size: 2em;
	}
	
	.cu-avatar.xl {
		width: 128upx;
		height: 128upx;
		font-size: 2.5em;
	}
	
	.cu-avatar .avatar-text {
		font-size: 0.4em;
	}
	
	.cu-avatar-group {
		direction: rtl;
		unicode-bidi: bidi-override;
		padding: 0 10upx 0 40upx;
		display: inline-block;
	}
	
	.cu-avatar-group .cu-avatar {
		margin-left: -30upx;
		border: 4upx solid #f1f1f1;
		vertical-align: middle;
	}
	
	.cu-avatar-group .cu-avatar.sm {
		margin-left: -20upx;
		border: 1upx solid #f1f1f1;
	}
	
	/* ==================
	         进度条
	 ==================== */
	
	.cu-progress {
		overflow: hidden;
		height: 28upx;
		background-color: #ebeef5;
		display: inline-flex;
		align-items: center;
		width: 100%;
	}
	
	.cu-progress+view,
	.cu-progress+text {
		line-height: 1;
	}
	
	.cu-progress.xs {
		height: 10upx;
	}
	
	.cu-progress.sm {
		height: 20upx;
	}
	
	.cu-progress view {
		width: 0;
		height: 100%;
		align-items: center;
		display: flex;
		justify-items: flex-end;
		justify-content: space-around;
		font-size: 20upx;
		color: #ffffff;
		transition: width 0.6s ease;
	}
	
	.cu-progress text {
		align-items: center;
		display: flex;
		font-size: 20upx;
		color: #333333;
		text-indent: 10upx;
	}
	
	.cu-progress.text-progress {
		padding-right: 60upx;
	}
	
	.cu-progress.striped view {
		background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
		background-size: 72upx 72upx;
	}
	
	.cu-progress.active view {
		animation: progress-stripes 2s linear infinite;
	}
	
	@keyframes progress-stripes {
		from {
			background-position: 72upx 0;
		}
	
		to {
			background-position: 0 0;
		}
	}
	
	/* ==================
	          加载
	 ==================== */
	
	.cu-load {
		display: block;
		line-height: 3em;
		text-align: center;
	}
	
	.cu-load::before {
		font-family: "cuIcon";
		display: inline-block;
		margin-right: 6upx;
	}
	
	.cu-load.loading::before {
		content: "\e67a";
		animation: cuIcon-spin 2s infinite linear;
	}
	
	.cu-load.loading::after {
		content: "加载中...";
	}
	
	.cu-load.over::before {
		content: "\e64a";
	}
	
	.cu-load.over::after {
		content: "没有更多了";
	}
	
	.cu-load.erro::before {
		content: "\e658";
	}
	
	.cu-load.erro::after {
		content: "加载失败";
	}
	
	.cu-load.load-cuIcon::before {
		font-size: 32upx;
	}
	
	.cu-load.load-cuIcon::after {
		display: none;
	}
	
	.cu-load.load-cuIcon.over {
		display: none;
	}
	
	.cu-load.load-modal {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 140upx;
		left: 0;
		margin: auto;
		width: 260upx;
		height: 260upx;
		background-color: #ffffff;
		border-radius: 10upx;
		box-shadow: 0 0 0upx 2000upx rgba(0, 0, 0, 0.5);
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		font-size: 28upx;
		z-index: 9999;
		line-height: 2.4em;
	}
	
	.cu-load.load-modal [class*="cuIcon-"] {
		font-size: 60upx;
	}
	
	.cu-load.load-modal image {
		width: 70upx;
		height: 70upx;
	}
	
	.cu-load.load-modal::after {
		content: "";
		position: absolute;
		background-color: #ffffff;
		border-radius: 50%;
		width: 200upx;
		height: 200upx;
		font-size: 10px;
		border-top: 6upx solid rgba(0, 0, 0, 0.05);
		border-right: 6upx solid rgba(0, 0, 0, 0.05);
		border-bottom: 6upx solid rgba(0, 0, 0, 0.05);
		border-left: 6upx solid #f37b1d;
		animation: cuIcon-spin 1s infinite linear;
		z-index: -1;
	}
	
	.load-progress {
		pointer-events: none;
		top: 0;
		position: fixed;
		width: 100%;
		left: 0;
		z-index: 2000;
	}
	
	.load-progress.hide {
		display: none;
	}
	
	.load-progress .load-progress-bar {
		position: relative;
		width: 100%;
		height: 4upx;
		overflow: hidden;
		transition: all 200ms ease 0s;
	}
	
	.load-progress .load-progress-spinner {
		position: absolute;
		top: 10upx;
		right: 10upx;
		z-index: 2000;
		display: block;
	}
	
	.load-progress .load-progress-spinner::after {
		content: "";
		display: block;
		width: 24upx;
		height: 24upx;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		border: solid 4upx transparent;
		border-top-color: inherit;
		border-left-color: inherit;
		border-radius: 50%;
		-webkit-animation: load-progress-spinner 0.4s linear infinite;
		animation: load-progress-spinner 0.4s linear infinite;
	}
	
	@-webkit-keyframes load-progress-spinner {
		0% {
			-webkit-transform: rotate(0);
			transform: rotate(0);
		}
	
		100% {
			-webkit-transform: rotate(360deg);
			transform: rotate(360deg);
		}
	}
	
	@keyframes load-progress-spinner {
		0% {
			-webkit-transform: rotate(0);
			transform: rotate(0);
		}
	
		100% {
			-webkit-transform: rotate(360deg);
			transform: rotate(360deg);
		}
	}
	
	/* ==================
