		/* margin-left: 30upx; */
		font-size: 30upx;
	}
	
	.cu-bar .action text.text-cut {
		text-align: left;
		width: 100%;
	}
	
	.cu-bar .cu-avatar:first-child {
		margin-left: 20upx;
	}
	
	.cu-bar .action:first-child>text[class*="cuIcon-"] {
		margin-left: -0.3em;
		margin-right: 0.3em;
	}
	
	.cu-bar .action:last-child {
		margin-right: 30upx;
	}
	
